import { useContext, useRef, useEffect, useState } from "react";
import { ShoppingCartContext } from "../../../App";


//const shoppingCart = new Set();



const CoctailCard = (props) => {
    const [click, setlick] = useState(0);

    const shoppingCart = useContext(ShoppingCartContext);

    const ref = useRef(null);

    useEffect(() => {
        if (shoppingCart.has(props.id)) {
            ref.current.innerHTML = ' ';
            ref.current.classList.add('added');
        }
    })


    let addToCard = (event) => {
        setlick(+1)
        if (!shoppingCart.has(props.id)) {
            shoppingCart.add(props.id);
            event.target.innerHTML = ' ';
            event.target.classList.add('added');
        } else {
            shoppingCart.delete(props.id);
            event.target.classList.remove('added');
            event.target.innerHTML = ' ';
        }
    }


    return (
        <div className="menu-item">
            <div className="item-title">{props.name}</div>
            <div className="item-img">
                <img src={props.img} alt="" />
            </div>
            <div className="item-price">{props.price} </div>
            <button ref={ref} onClick={addToCard} className='main-button'> </button>
        </div>
    );
}

export default CoctailCard;